import { useContext } from 'react'
import { PageWidth } from '../../../components/general'
import { ErrorMsg } from '../../../components/loginSystem/loginCard'
import {
  MsgTitle,
  FormTitle,
  Steps,
  FormWrapper,
  Form,
  InputWrapper,
  Label,
  RadionLabel,
  InputTitle,
  Input,
  BtnFlexCenter
} from '../../../components/checkoutSystem/Step'
import { useForm } from 'react-hook-form'
import { GeneralBtn } from '../../../components/Button'
import { UserContext } from '../../../context'
import Login from '../../Login/Login'
import { COLOR } from '../../../constants/style'
export default function Step2() {
  const { user } = useContext(UserContext) // memberId
  const {
    register,
    formState: { errors },
    handleSubmit
  } = useForm()
  const onSubmit = (submitData) => {
    console.log(submitData)
  }
  return (
    <PageWidth>
      <Steps />
      {user ? (
        <Form onSubmit={handleSubmit(onSubmit)}>
          <div>
            <FormWrapper>
              <FormTitle></FormTitle>
              <InputWrapper>
                <InputTitle children=':' />
                <Input
                  type='email'
                  {...register('orderEmail', { required: true })}
                />
              </InputWrapper>
              <ErrorMsg>
                {errors.orderEmail?.type === 'required' && ''}
              </ErrorMsg>
              <InputWrapper>
                <InputTitle children=':' />
                <Input
                  type='text'
                  {...register('orderName', { required: true })}
                />
              </InputWrapper>
              <ErrorMsg>
                {errors.orderName?.type === 'required' && ''}
              </ErrorMsg>
              <InputWrapper>
                <InputTitle children=':' />
                <Input
                  type='text'
                  {...register('orderAddress', { required: true })}
                />
              </InputWrapper>
              <ErrorMsg>
                {errors.orderAddress?.type === 'required' && ''}
              </ErrorMsg>
              <InputWrapper>
                <InputTitle children=':' />
                <Input
                  type='tel'
                  {...register('orderPhone', {
                    required: true,
                    pattern: /^[0-9]{10}$/
                  })}
                />
              </InputWrapper>
              <ErrorMsg>
                {errors.orderPhone?.type === 'required' && ''}
                {errors.password?.type === 'pattern' && ''}
              </ErrorMsg>
            </FormWrapper>
            <FormWrapper>
              <FormTitle></FormTitle>
              <Label>
                <Input
                  type='radio'
                  id='cash'
                  name='pay'
                  value=''
                  {...register('payment', { required: true })}
                />
                <RadionLabel children='' />
              </Label>

              <Label>
                <Input
                  type='radio'
                  id='creditCard'
                  name='pay'
                  value=''
                  {...register('payment', { required: true })}
                />
                <RadionLabel children='' />
              </Label>
              <ErrorMsg>
                {errors.payment?.type === 'required' && ''}
              </ErrorMsg>
              <FormTitle></FormTitle>
              <Label>
                <Input
                  type='radio'
                  id='home'
                  name='delivery'
                  value=''
                  checked
                  {...register('shipping', { required: true })}
                />
                <RadionLabel children='' />
              </Label>

              {/* <Label>
            <Input type='radio' id='store' name='delivery' value='store' />
            <RadionLabel children='' />
          </Label> */}
            </FormWrapper>
          </div>
          <BtnFlexCenter>
            <GeneralBtn
              color='primary'
              marginStyle={{ marginTop: '20px' }}
              children=''
            />
          </BtnFlexCenter>
        </Form>
      ) : (
        <>
          <MsgTitle>
            <span style={{ color: COLOR.text_warning }}></span>
            
          </MsgTitle>
          <Login $location={'/checkout/step2'} />
          <BtnFlexCenter>
            <GeneralBtn
              color='light_grey'
              marginStyle={{ cursor: 'not-allowed' }}
              children=''
            />
          </BtnFlexCenter>
        </>
      )}
    </PageWidth>
  )
}
