import { useContext } from 'react'
import { Link } from 'react-router-dom'
import { PageWidth } from '../../../components/general'
import {
  Title,
  Steps,
  TitleWidth,
  TitleGroup,
  CartTitles,
  CartTitle,
  TotalPrice,
  BtnFlexCenter,
  DeliveryData
} from '../../../components/checkoutSystem/Step'
import { GeneralBtn } from '../../../components/Button'
import { OrderItem } from './OrderItem'
import { LocalStorageContext } from '../../../context'

export default function Step3() {
  const { cartItems, totalPrice } = useContext(LocalStorageContext)
  return (
    <PageWidth>
      <Steps />

      <Title></Title>
      <DeliveryData>: username</DeliveryData>
      <DeliveryData>: 1234@gmail.com</DeliveryData>
      <DeliveryData>: fullname</DeliveryData>
      <DeliveryData>: 11132</DeliveryData>
      <DeliveryData>: 0912345678</DeliveryData>

      {/* <Title></Title>
          <DeliveryData></DeliveryData>
          <Title></Title>
          <DeliveryData></DeliveryData> */}

      <Title></Title>
      <CartTitles>
        <TitleWidth />
        <TitleGroup>
          <CartTitle></CartTitle>
          <CartTitle></CartTitle>
          <CartTitle></CartTitle>
        </TitleGroup>
      </CartTitles>
      {cartItems.map((item) => (
        <OrderItem key={item.id} item={item} />
      ))}
      <TotalPrice>{` NT$ ${totalPrice}`}</TotalPrice>
      <BtnFlexCenter>
        <Link to='/'>
          <GeneralBtn color='primary' children='' />
        </Link>
      </BtnFlexCenter>
    </PageWidth>
  )
}
